import React from "react";
import { Card, CardBody, CardTitle, CardImg } from "reactstrap";

class Redirect extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      step: props.step
    };
  }

  doRedirect() {
    window.open(this.state.step.content, this.state.step.target).focus();
  }

  render() {
    return (
      <Card className="h-100 cursor-pointer" onClick={() => { this.doRedirect() }}>
        <CardImg top className="img-fluid" src={this.state.step.image} />
        <CardBody>
          <CardTitle className="h4 mt-0">{this.state.step.title}</CardTitle>
        </CardBody>
      </Card>
    );
  }
}

export default Redirect;