import React from "react";
import { Card, CardBody, CardTitle, CardText, CardImg } from "reactstrap";

class Text extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      step: props.step
    };
  }

  render() {
    return (
      <Card className="h-100">
        <CardImg top className="img-fluid" src={this.state.step.image} />
        <CardBody>
          <CardTitle className="h4 mt-0">{this.state.step.title}</CardTitle>
          <CardText dangerouslySetInnerHTML={{ __html: this.state.step.content.replace(/(<? *script)/gi, 'illegalscript') }}>
          </CardText>
        </CardBody>
      </Card>
    );
  }
}

export default Text;