import React from "react";
import PropTypes from "prop-types";
import { Route } from "react-router-dom";

import Home from "../pages/Home";
import Step from "../pages/Step";

const getWizardRoutes = (array) => {
  var result = [];
  array.forEach(function (a) {
    result.push(a);
    if (Array.isArray(a.options)) {
      result = result.concat(getWizardRoutes(a.options));
    }
  });

  return result;
}

let wizardRoutes = [];
wizardRoutes = getWizardRoutes(window.WIZARD_CONFIG.options);
wizardRoutes.unshift({ path: "/", id: "home" });

const AppRoute = ({
  component: Component,
  layout: Layout,
  ...rest
}) => (
  wizardRoutes.map((step) => {
    return (
      <Route
        exact
        path={step.id == "home" ? '/' : '/' + step.id}
        key={step.id}
        children={step.id == "home" ? <Home /> : <Step step={step} />} />
    )
  })
)

AppRoute.propTypes = {
  component: PropTypes.any,
  location: PropTypes.object,
  layout: PropTypes.any
};

export default AppRoute;
